import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import {MatSort} from '@angular/material/sort';
import { MonitoringService } from '../monitoring.service';
import { Event } from '../interfaces/event';

@Component({
  selector: 'event-list',
  templateUrl: './event-list.component.html',
  styleUrls: ['./event-list.component.scss']
})
export class EventListComponent implements AfterViewInit {

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: false}) sort: MatSort;
  displayedColumns: string[] = ['account', 'device', 'status', 'type', 'event', 'created'];
  data: any;
  resultsLength = 0;
  eventList: Event[] = [];

  constructor(public monitoringService: MonitoringService) { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    this.loadEvents();
  }

  applyFilter(filterValue: string) {
    this.data.filter = filterValue.trim().toLowerCase();

    if (this.data.paginator) {
      this.data.paginator.firstPage();
    }
  }

  loadEvents() {
    this.monitoringService.getAllEvents()
    .subscribe(data => {
      this.generateDataToTable(data);
      this.data = new MatTableDataSource<Event>(this.eventList);
      this.data.paginator = this.paginator;
      console.log("Load Events success! " );
    });
  }

  refreshEvents() {
    this.eventList = [];
    this.loadEvents();
  }

  generateDataToTable(eventsData: any){
    eventsData.forEach(eventx => {
      eventx.events.forEach(eventy => {
        const eventz = {
          account: eventx.account,
          device: eventx.device,
          type: eventy.type,
          event: eventy.event,
          registerDate: eventy.registerDate,
          status: eventx.status,
          eventDate: eventx.eventDate
        };
        this.eventList.push(eventz);
      });
    });
  }

}
