import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import {MatSort} from '@angular/material/sort';
import { MonitoringService } from '../monitoring.service'
import { CryptoTool } from '../../core/crypto/crypto-tool'
import { Event } from '../interfaces/event';

@Component({
  selector: 'event-account-list',
  templateUrl: './event-account-list.component.html',
  styleUrls: ['./event-account-list.component.scss']
})
export class EventAccountListComponent implements AfterViewInit {

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: false}) sort: MatSort;
  displayedColumns: string[] = ['device', 'status', 'type', 'event', 'created'];
  data: any;
  resultsLength = 0;
  eventList: Event[] = [];

  constructor(public monitoringService: MonitoringService, private cryptoTool: CryptoTool) { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    this.loadEvents();
  }

  applyFilter(filterValue: string) {
    this.data.filter = filterValue.trim().toLowerCase();

    if (this.data.paginator) {
      this.data.paginator.firstPage();
    }
  }

  loadEvents() {
    const account = localStorage.getItem('vp')
    this.monitoringService.getEventsByAccount(this.cryptoTool.decryptString(account))
    .subscribe(data => {
      this.generateDataToTable(data);
      this.data = new MatTableDataSource<Event>(this.eventList);
      this.data.paginator = this.paginator;
      console.log("Load Events success! " );
    });
  }

  refreshEvents() {
    this.eventList = [];
    this.loadEvents();
  }

  generateDataToTable(eventsData: any){
    eventsData.events.forEach(eventx => {
      const eventy = {
        account: eventsData.account,
        device: eventsData.device,
        type: eventx.type,
        event: eventx.event,
        registerDate: eventx.registerDate,
        status: eventsData.status,
        eventDate: eventsData.eventDate
      };
      this.eventList.push(eventy);
    });
  }

}
