import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MonitoringComponent } from './monitoring.component';
import { AdministrationComponent } from './administration/administration.component';
import { CommandBaseComponent } from './command-base/command-base.component';
import { EventAccountListComponent } from './event-account-list/event-account-list.component';

const routes: Routes = [
  {
    path: '',
    component: MonitoringComponent,
    //canActivate: [AuthGuard],
    children: [
      { path: 'administration', component: AdministrationComponent },
      {
        path: 'base',
        component: CommandBaseComponent,
        children: [
          {
            path: 'events-account',
            component: EventAccountListComponent
          }
        ]
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MonitoringRoutingModule { }
